        pageSize: 6,
        departmentInfoId: null
      },
    }
  },
  mounted() {
    this.getExpertsList()
  },
  methods:{
    getExpertsList(pages = 1){
      this.expertsQuery.pageNum = pages
      this.$store.dispatch('experts/getExpertsList',this.expertsQuery)
    }
  },
